<template>
	<!-- 设置界面 -->
	<view>
		<!--菜单列表-->
		<view class="cu-list menu">
			<view class="cu-item arrow">
				<text class="text-black text-blod">头像</text>
				<view class="action">
					<button class="cu-btn round bg-green shadow" @tap="uploadPhoto">
						<text class="cuIcon-upload"></text> 上传</button>
				</view>
			</view>
			<view class="cu-item arrow">
				<text class="text-black text-blod">性别</text>
					<picker @change="PickerChange" :value="index" :range="picker">
						<view class="picker">
							{{index>-1?picker[index]:'请选择'}}
						</view>
					</picker>
			</view>
			
			<view class="cu-item arrow margin-top-sm">
				<text class="text-black text-blod">手机号</text>
				<view class="content margin-lr-xs">
					<input placeholder="输入手机号" name="input"></input>
				</view>
			</view>
			<view class="padding flex flex-direction">
				<button class="cu-btn bg-blue margin-tb-sm lg" @tap="handelLogout">修改信息</button>
			</view>  
		
		</view>
	</view>
</template>

<script>
	export default {
		data(){
			return{
				index: -1,
				picker: ['请选择','男', '女'],
				photo:''
			}
		},
		methods:{
			uploadPhoto(){
				uni.chooseImage({
					count: 1, //默认1
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: (res) => {
						if (this.photo.length != 0) {
							this.photo = this.photo.concat(res.tempFilePaths)
							
						} else {
							this.photo = res.tempFilePaths
						}
					}
				});
			},
			handelLogout(){
				uni.showToast({
					icon: 'none',
					title: '退出成功'   
				});
			},
			PickerChange(e) {
				this.index = e.detail.value
			},
		}
	}
</script>

<style>
	.end-bottom{
		align-self: flex-end;
	}
</style>
